import { useEffect, useState } from 'react';
import axios from 'axios';
import { useRouteMatch } from 'react-router';
import Countrycard from './Countrycard';

function SearchedContent() {
	const input = useRouteMatch();
	const s = input.params.s;
	console.log(input.params.s);
	const [data, setData] = useState([]);

	useEffect(() => {
		const call = async () => {
			const res = await axios.get(`https://restcountries.eu/rest/v2/name/${s}`);
			const dataQuery = res.data;
			setData(dataQuery);
		};
		call();
	}, [s]);
	const Text = () => {
		return (
			<h3 className='text-md flex justify-center mt-6 text-xl'>
				You Seached For{' '}
				<span className='font-semibold'>"{input.params.s}"</span>
			</h3>
		);
	};
	if (data.length > 0) {
		return (
			<div className={``}>
				<Text />
				<article className='p-6 grid md:gap-4 md:grid-cols-3 lg:grid-cols-4'>
					{data.map((content) => {
						return <Countrycard key={content.name} content={content} />;
					})}
				</article>
			</div>
		);
	} else {
		return (
			<div className='flex items-center justify-center p-6 w-full h-full font-bold text-2xl '>
				<p className=''>loading...</p>
			</div>
		);
	}
}

export default SearchedContent;
