import { useState, useEffect } from 'react';
import CountryCard from './Countrycard';
import axios from 'axios';
function Home() {
	const [data, setData] = useState([]);
	const [limit, setLimit] = useState(25);

	useEffect(() => {
		const call = async () => {
			const res = await axios.get(`https://restcountries.eu/rest/v2/all`);
			const dataQuery = res.data.slice(0, limit);
			setData(dataQuery);
		};
		call();
	}, [limit]);

	return (
		<>
			<section className='p-6 md:grid md:grid-cols-3 lg:grid-cols-4 md:gap-6'>
				{data.map((data) => {
					return <CountryCard key={data.alpha2Code} content={data} />;
				})}
			</section>
			<button
				onClick={() => {
					setLimit(limit + 25);
				}}
				className='show-more flex mx-auto mt-8 p-2 mb-3 text-white bg-red-500 outline-none'
			>
				Show More !!!
			</button>
		</>
	);
}

export default Home;
