import { useRef, useState } from 'react';
import { Link } from 'react-router-dom';
function SearchInput() {
	const inputContent = useRef();
	const [input, setInput] = useState('');
	const handleChange = () => {
		setInput(inputContent.current.value);
	};
	return (
		<section className='input flex items-center py-3 px-1 w-full bg-gray-100'>
			<input
				ref={inputContent}
				onChange={() => {
					handleChange();
				}}
				name='search bar'
				id='search'
				className='w-full p-2 outline-none'
				placeholder='Search for a country...'
			/>
			<button className='rounded-lg bg-gray-700 p-2 md:w-20 w-1/4 text-white'>
				<Link to={`/search/${input}`}>
					<i class='fas fa-search'></i>
				</Link>
			</button>
		</section>
	);
}

export default SearchInput;
