import { useEffect, useState } from 'react';
import { Link, useRouteMatch } from 'react-router-dom';
import axios from 'axios';
function SingleCountry() {
	let match = useRouteMatch();

	const { id } = match.params;
	const [data, setData] = useState({});
	useEffect(() => {
		const call = async () => {
			const res = await axios.get(
				`https://restcountries.eu/rest/v2/alpha/${id}`
			);
			setData(await res.data);
		};
		call();
	}, [id]);
	const Meta = ({ content }) => {
		return (
			<div className='p-6'>
				<button className='focus-within:outline-none border-2 font-semibold text-lg py-1 px-2'>
					<Link to='/' className='flex gap-2 items-center'>
						<i class='fas fa-long-arrow-alt-left'></i>Back
					</Link>
				</button>
				<div className='lg:grid lg:grid-cols-2 lg:gap-10 mt-10'>
					<img
						src={content.flag}
						alt={content.name}
						className='w-full lg:mx-0 md:mx-auto'
					/>
					<div className='meta md:space-y-0 mt-10'>
						<h2 className='font-bold mb-6 text-xl'>{content.name}</h2>
						<div className='md:flex w-full justify-between'>
							<div className='left'>
								<p>
									Native Name: <span>{content.nativeName}</span>
								</p>
								<p>
									Population:{' '}
									<span>
										{new Intl.NumberFormat().format(content.population)}
									</span>
								</p>
								<p>
									Region: <span>{content.region}</span>
								</p>
								<p>
									Sub Region: <span>{content.subregion}</span>
								</p>
								<p>
									Capital: <span>{content.capital}</span>
								</p>
							</div>

							<div className='right md:mt-0 mt-6'>
								<p>
									Top Level Domain:{' '}
									{content.topLevelDomain.map((domain) => {
										return <span key={domain[0]}>{domain}</span>;
									})}
								</p>
								<p>
									Currencies:{' '}
									{content.currencies.map((currency) => {
										return <span key={currency[0]}>{currency.name}</span>;
									})}
								</p>
								<p>
									Languages:{' '}
									{content.languages.map((lang) => {
										return <span>{lang.name} , </span>;
									})}
								</p>
							</div>
						</div>
					</div>
				</div>
			</div>
		);
	};

	if (data.name) {
		return <Meta content={data} />;
	} else {
		return (
			<div className='flex items-center justify-center p-6 w-full h-full font-bold text-2xl '>
				<p className=''>loading...</p>
			</div>
		);
	}
}

export default SingleCountry;
