import React from 'react';
import { Link } from 'react-router-dom';
// import SingleCountry from './SingleCountry';
function Countrycard({ content }) {
	const { name, flag, population, region, capital, alpha3Code } = content;
	return (
		<>
			<Link to={`/country/${alpha3Code}`}>
				<div className={`shadow-lg rounded-md space-y-4 pb-4 mt-10 h-full`}>
					<img src={`${flag}`} alt={`${name}`} className='rounded-md' />
					<div className='content px-4 py-2'>
						<h2 className={`title font-bold text-md`}>{name}</h2>
						<div className={` meta-data capitalize font-medium  pt-2`}>
							<h4 className='population '>
								population:
								<span className=' ml-2 font-thin'>
									{new Intl.NumberFormat().format(population)}
								</span>
							</h4>
							<h4 className='region '>
								region:
								<span className='ml-2 font-thin'>{region}</span>
							</h4>
							<h4 className='capital '>
								capital:
								<span className='ml-2 font-thin'>{capital}</span>
							</h4>
						</div>
					</div>
				</div>
			</Link>
		</>
	);
}

export default Countrycard;
